import React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Jobs from "../components/jobs"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"
import "../components/utility.css"
import ProjectCard from "../components/projectCard"

import financePhoto from "/Users/alex/dev/learning-projects/gatsbyjs/alex-love.github.io/content/assets/finance-calc.png"
import survPhoto from "/Users/alex/dev/learning-projects/gatsbyjs/alex-love.github.io/content/assets/surv-sol.png"

class BlogIndex extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showJobs: false,
      className: "hide",
    }
    this.toggleJobs = this.toggleJobs.bind(this)
  }

  toggleJobs() {
    //hacky way to do what I want.. change this later
    if (this.state.className === "show") {
      this.setState({ className: "hide", showJobs: false })
    } else {
      this.setState({ className: "show", showJobs: true })
    }
  }

  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges
    const { showJobs, className } = this.state

    //move these to my json file
    const financeDescription =
      "Work in progress but I'm currently working on the backend. Test calculator/graph is up as a test for  "

    const surv =
      "This was a website that I made for a client using html/css/js and Bootstrap. The goal was to quickly create a responsive website that resembled a previous website made using godaddy pagebuilder."

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO
          title="All posts"
          keywords={[`blog`, `gatsby`, `javascript`, `react`, "resume"]}
        />

        <div style={{ flexBasis: "2" }}>
          <h3>Projects and Work</h3>
        </div>
        {/* <Bio />  add this data to a json file at some point and then just map it*/}
        <div className="project-container">
          <ProjectCard
            name={"Finance App"}
            id={0}
            picture={financePhoto}
            description={financeDescription}
            link={"https://alex-love.github.io/finance-app/"}
          />
          <ProjectCard
            name={"Static Website"}
            id={1}
            picture={survPhoto}
            description={surv}
            link={"https://surveillancesolutions.org"}
          />
        </div>
        <button className="button" onClick={this.toggleJobs}>
          {showJobs ? "Hide Resume" : "View Resume"}
        </button>
        {/* Conditionally Render Jobs and update className for fade in animation */}
        <div className={className}>{showJobs && <Jobs />}</div>
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          return (
            <div key={node.fields.slug}>
              <h3
                style={{
                  marginBottom: rhythm(1 / 4),
                }}
              >
                <Link style={{ boxShadow: `none` }} to={node.fields.slug}>
                  {title}
                </Link>
              </h3>
              <small>{node.frontmatter.date}</small>
              <p
                dangerouslySetInnerHTML={{
                  __html: node.frontmatter.description || node.excerpt,
                }}
              />
            </div>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { published: { eq: true } } }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
            published
          }
        }
      }
    }
  }
`
